.mainBanner {
  height: 241px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  border: 1px solid #dee4e7;
  background: #fff;
}
.mainBanner h4 {
  color: #222525;
  font-family: "Gilroy-semibold";
  font-size: clamp(48px, 7vw, 64px);
}

.resourcesContainer {
  min-height: 298px;
  width: 100%;
  border-radius: 20px;
  border: 1px solid #dee4e7;
  background: #fff;
  padding: 28px;
}

.resourcesSection {
  text-align: left;
}
.resourcesSection {
  height: 100%;
}

.logoContainer {
  width: 200px;
  height: 200px;
  border-radius: 14px;
  background: linear-gradient(
      0deg,
      rgba(0, 0, 0, 0.2) 0%,
      rgba(0, 0, 0, 0.2) 100%
    ),
    #222525;
  margin-top: auto;
  position: relative; /* теперь внутри можно абсолютное позиционирование */
}
.resourcesSection h6 {
  color: #222525;
  font-family: "Gilroy-semibold";
  font-size: 20px;
}

.colorsContainer {
  display: flex;
  flex-direction: column;
  /* оставляем justify-content: flex-start (по умолчанию) */
  height: 200px;
}
.colorsHalf {
  display: flex;
  align-items: center;
}
/* первый блок прижмём к верху */
.colorsHalf:first-child {
  margin-bottom: auto;
}

/* последний блок прижмём к низу */
.colorsHalf:last-child {
  margin-top: auto;
}

.colorsHalf h6 {
  color: #646474;
  font-size: 20px;
  font-family: "Gilroy-medium";
  margin: 2px;
}
.rightStick {
  height: 100%;
  width: 1px;
  background-color: #dee4e7;
}
.divider {
  width: 100%;
  height: 1px;
  background-color: #dee4e7;
  margin: auto 0;
}

.logoHalf {
  display: flex;
  align-items: center;
  border-radius: 14px;
  background: #222525;
  padding: 16px;
  height: 84px;
}
/* первый блок прижмём к верху */
.logoHalf:first-child {
  margin-bottom: auto;
}

/* последний блок прижмём к низу */
.logoHalf:last-child {
  margin-top: auto;
}

.logoHalf h6 {
  color: #646474;
  font-size: 20px;
  font-family: "Gilroy-medium";
  margin: 2px;
}
.picDownloadContainer {
  display: flex;
  width: 45px;
  height: 22px;
  padding: 5px 6px;
  justify-content: center;
  align-items: center;
  border-radius: 6px;
  background: #fff;

  color: #222525;
  font-size: 16px;
  font-family: "Gilroy-semibold";
  cursor: pointer;
}
.logoIcon {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 113px; /* или любое ваше значение */
  height: auto;
  transform: translate(-50%, -50%);
}

.picDownloadContainerLogo {
  display: flex;
  width: 45px;
  height: 22px;
  padding: 5px 6px;
  justify-content: center;
  align-items: center;
  border-radius: 6px;
  background: #fff;

  color: #222525;
  font-size: 16px;
  font-family: "Gilroy-semibold";
  cursor: pointer;
}
.picDownloadBtns {
  position: absolute;
  bottom: 12px;
  right: 12px;
}
